html {
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
}

.Covid-holder {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  position: relative;
}

.covid-wrapper {
  position: relative;
}

.covid-sticks {
  position: absolute;
  bottom: 33px;
  left: 36px;
}

.covid {
  background-image: url("https://www.transparenttextures.com/patterns/dark-stripes.png"),
    linear-gradient(
      to left top,
      #9d1231,
      #ae1a38,
      #be2240,
      #d02a47,
      #e1324f,
      #ea4153,
      #f34e58,
      #fc5b5d,
      #fd6e61,
      #fe7f68,
      #ff8f70,
      #ff9e7a
    );
  width: 410px;
  height: 410px;
  z-index: 2;
  overflow: hidden;
  position: relative;
  border-radius: 50%;
  -webkit-animation: pulse 2s infinite;
  animation: pulse 2s infinite;
}
.covid-pulse {
  content: "";
  width: 480px;
  height: 480px;
  /* background-color: #2f184e; */
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  /* box-shadow: inset 0 0 25px #8f2453; */
}

@-webkit-keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(204, 169, 44, 0.21);
  }
  70% {
    box-shadow: 0 0 8px 55px rgba(99, 32, 91, 0.1);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(99, 32, 91, 0.1);
  }
}

@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(204, 169, 44, 0.21);
  }
  70% {
    box-shadow: 0 0 8px 55px rgba(99, 32, 91, 0.1);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(99, 32, 91, 0.1);
  }
}
