@-webkit-keyframes slip {
  0% {
    transform: translate(0px, 0px);
  }
  20% {
    transform: translate(-6px, -6px);
  }
  40% {
    transform: translate(0px, 0px);
  }
  60% {
    transform: translate(-10px, -2px);
  }
  100% {
    transform: translate(0px, 0px);
  }
}

@keyframes slip {
  0% {
    transform: translate(0px, 0px);
  }
  20% {
    transform: translate(-6px, -6px);
  }
  40% {
    transform: translate(0px, 0px);
  }
  60% {
    transform: translate(-10px, -2px);
  }
  100% {
    transform: translate(0px, 0px);
  }
}
.covid-eyes {
