}

@-webkit-keyframes eyes-blink {
  0% {
    transform: scaleY(1);
  }
  2% {
    transform: scaleY(0.1);
  }
  4% {
    transform: scaleY(1);
  }
  6% {
    transform: scaleY(0.1);
  }
  8% {
    transform: scaleY(1);
  }
  66% {
    transform: scaleY(1);
