  .earth,
  .earth-detail {
    transform: scale(0.8);
  }

  .virus {
    display: none;
  }

  .covid-arms {
    transform: scale(0.8) rotate(26deg) skew(57deg, -5deg);
    bottom: 98px;
    left: 87px;
  }

  .covid-arms.right {
    transform: scale(0.8) rotate(146deg) skew(24deg, 5deg);
    bottom: 90px;
    left: 236px;
